<template>
  <MainScreen
    v-if="statusMath === 'default'"
    @onGame="handleOnStartGame($event)"
  ></MainScreen>
  <PlayScreen v-if="statusMath === 'math'" :gameStart="setting.gameStart" @onFinish="onFinishGame()"></PlayScreen>
  <FinishScreen v-if="statusMath === 'finish'" :timer="timer" @onReStartGame="statusMath = 'default'"></FinishScreen>
  <CoppyRightScreen></CoppyRightScreen>
</template>

<script>
import MainScreen from "./components/MainScreen.vue";
import PlayScreen from "./components/PlayScreen.vue";
import { shuffled } from "./assets/utils/array"
import FinishScreen from "./components/FinishScreen.vue";
import CoppyRightScreen from "./components/CoppyRightScreen.vue";

export default {
  name: "App",
  components: {
    MainScreen,
    PlayScreen,
    FinishScreen,
    CoppyRightScreen,
  },
  data() {
    return {
      statusMath: "default",
      setting: {
        toltalStartGame: 0,
        gameStart: [],
        startedAt: null,
      },
      timer: 0,
    };
  },
  methods: {
    handleOnStartGame(config) {
      console.log("running handle starts", config);
      this.statusMath = "math";
      this.setting.toltalStartGame = config.toltalStartGame;
      const firstCard = Array.from({ length: this.setting.toltalStartGame / 2 }, (_,i) => i + 1);
      const towCard = [...firstCard];
      const newCard = [...firstCard, ...towCard];
      console.log(newCard);
      this.setting.gameStart = shuffled(shuffled(shuffled(shuffled(newCard))));
      console.log(this.setting.gameStart);
      this.setting.startedAt = new Date().getTime();
    },
    onFinishGame() {
      this.timer = new Date().getTime() - this.setting.startedAt;
      this.statusMath = "finish";
    },
  },
};
</script>
