<!-- eslint-disable vue/multi-word-component-names -->
<template>
  <div class="card" 
  :class="{ disable: isDisale}" 
  :style="{ height: `${(920 - 16 * 4) / Math.sqrt(gameStart.length) - 16}px`,
            width: `${(((920 - 16 * 4) / Math.sqrt(gameStart.length) - 16) * 3) / 4}px`,
            perspective: `${(((920 - 16 * 4) / Math.sqrt(gameStart.length) - 16) * 3)  / 4 * 2 }px`}">
    <div
      class="card_inner"
      :class="{ 'is-flipped': isFlipped }"
      @click="onToggleOnChage"
    >
      <div class="card_face card_show_one">
        <div class="card_content" :style="{ backgroundSize: `${(((920 - 16 * 4) / Math.sqrt(gameStart.length) + 16) * 3) / 4 / 3}px, 
        ${(((920 - 16 * 4) / Math.sqrt(gameStart.length) + 16) * 3) / 4 / 3}px`
          }"></div>
      </div>
      <div class="card_face card_show_two">
        <div class="card_content" :style="{ backgroundImage: `url(${require('@/assets' + imageOnGame)})`}"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    card: {
      type: [String, Array, Number, Object],
    },

    imageOnGame: {
      type: String,
      require: true,
    },
    gameStart: {
      type: Array,
      default: function() {
        return []
      }
    },
    rule: {
      type: Array,
      default: function() {
        return []
      }
    }
  },
  data() {
    return {
      isFlipped: false,
      isDisale: false,
    };
  },
  methods: {
    onToggleOnChage() {
      if(this.rule.length === 2) return false;
      console.log(this.rule);
      if(this.isDisale) return false;
      this.isFlipped = !this.isFlipped;
      if(this.isFlipped) this.$emit("onFlip", this.card)

    },
    onFlipBackCard() {
      this.isFlipped = false;
    },
    onEnableDisableCard() {
      this.isDisale = true;
    }
  },
};
</script>

