<template>
  <div class="screen">
    <div class="screen_inner" :style="{ 
      width: `${((((920 - 16 * 4) / Math.sqrt(gameStart.length) + 16) * 3) / 4 + 16) * Math.sqrt(gameStart.length)}px`,
    }">
      <Card 
      v-for="(card, index) in gameStart" :key="index"
      :ref="`card-${index}`" 
      :imageOnGame="`/images/${card}.png`" 
      :card="{ index, value: card }" 
      @onFlip="checkRule($event)"
      :gameStart="gameStart"
      :rule="rule">
      </Card>
    </div>
  </div>
</template>

<script>
import Card from "../components/Card.vue";
export default {
  props: {
    gameStart: {
      type: Array,
      default: function() {
        return []
      }
    },
  },
  data() {
    return {
      rule: [],
    }
  },
  components: {
    Card,
  },
  methods: {
    checkRule(card) {
      if(this.rule.length === 2) return false;
      this.rule.push(card);
      if(this.rule.length === 2 && this.rule[0].value === this.rule[1].value)
      {
        console.log("Right...")
        this.$refs[`card-${this.rule[0].index}`][0].onEnableDisableCard();
        this.$refs[`card-${this.rule[1].index}`][0].onEnableDisableCard();
        this.rule = [];
        const disabledElement = document.querySelectorAll(".screen .card.disable");
        if( disabledElement && disabledElement.length === this.gameStart.length - 2) {
          setTimeout(() => {
            this.$emit("onFinish");
          },920);
        }
      }
      else if(this.rule.length === 2 && this.rule[0].value !== this.rule[1].value)
      {
        setTimeout(() => {
          this.$refs[`card-${this.rule[0].index}`][0].onFlipBackCard();
          this.$refs[`card-${this.rule[1].index}`][0].onFlipBackCard();
          this.rule = [];
          console.log("Wrong...");
        },800)
      }
      else return false;
    },
  }, 
};
</script>

<style lang="css" scoped>
  .screen {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    background-color: var(--dark);
    color: var(--light);
  }

  .screen_inner {
    display: flex;
    flex-wrap: wrap;
    margin: 2rem auto;
  }
</style>
